import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FeedbackComponent } from './feedback/feedback.component';
import { UserComponent } from './user/user.component';

const routes: Routes = [
  { path: 'messaging', loadChildren: () => import('./messaging/messaging.module').then(m => m.MessagingModule) },
  { path: 'set-me-up/:roomCode', component: UserComponent},
  { path: '', loadChildren: () => import('./share-link/share-link.module').then(m => m.ShareLinkModule) },
  { path: 'feedback', component: FeedbackComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
