import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ShareJoiningLinkComponent } from './share-joining-link/share-joining-link.component';
import { ShareLinkRoutingModule } from './share-link-routing.module';
import {MatCardModule} from '@angular/material/card';
import {ClipboardModule} from '@angular/cdk/clipboard';
import {MatSnackBarModule} from '@angular/material/snack-bar';
import { MatButtonModule } from '@angular/material/button';
import { FaqInfoComponent } from './faq-info/faq-info.component';
import {MatExpansionModule} from '@angular/material/expansion';


@NgModule({
  declarations: [ShareJoiningLinkComponent, FaqInfoComponent],
  imports: [
    CommonModule,
    ShareLinkRoutingModule,
    MatCardModule,
    ClipboardModule,
    MatButtonModule,
    MatSnackBarModule,
    MatExpansionModule
  ],
  exports: [FaqInfoComponent]
})
export class ShareLinkModule { }
