import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { ErrorHandlerService } from '../errorHandler/error-handler.service';
import { Globals } from '../../global.constants';

@Injectable({
  providedIn: 'root'
})
export class LinkGenerationService {
  data: any;
  private REST_API_SERVER = Globals.ENDPOINT + '/link';

  constructor(private httpClient: HttpClient, private errorHandlerService: ErrorHandlerService) { }

  public getLink(): any {
    this.data = this.httpClient.get(this.REST_API_SERVER)
    .pipe(
           catchError(this.errorHandlerService.handleError<any>('Link Generation'))
    );
    return this.data;
  }

}
