import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Globals } from '../global.constants';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.css']
})
export class FeedbackComponent implements OnInit{

  formGroup!: FormGroup;
  genderAlert: string = "Please enter Male/Female/Others";
  recommendToFriendAlert: string = "Please enter Yes/No/Maybe";
  userAlert: string = "Please enter minimum 5 characters"
  loading: boolean = false;
  message = {}

  constructor(private router: Router, private http: HttpClient, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.createForm();
  }

  createForm() {
    this.formGroup = this.formBuilder.group({
      gender: [null, [Validators.required, Validators.minLength(3),Validators.maxLength(6)]],
      recommendToFriend: [null, [Validators.required, Validators.minLength(2), Validators.maxLength(5)]],
      userLiked: [null, [Validators.required, Validators.minLength(5)]],
      userWants: [null, [Validators.required, Validators.minLength(5)]],
    });
  }

  get recommendToFriend() {
    return this.formGroup.get("recommendToFriend") as FormControl;
  }

  onSubmit(post: any) {
    this.message = JSON.stringify(post);
    this.loading = true;
    this.http.post(Globals.ENDPOINT + Globals.FEEDBACK_ENDPOINT, this.message,
      { headers: { 'Content-Type': 'application/json; charset=utf-8' } })
      .subscribe((m) => this.router.navigate(['']));
  }
}
