import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { Globals } from '../global.constants';
import { UserService } from '../user.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  username!: string;
  token!: string;
  errorMessage!: string;
  preventRoomEntry = true;
  noErrors = true;
  loading: boolean = false;

  constructor(private userService: UserService, private router: Router,
    private activateRoute: ActivatedRoute, private http: HttpClient,
    private snackBar: MatSnackBar) { }

  ngOnInit(): void {
    /* check if the room code exists
    * is valid
    * two people are not already in the room
    * not closed
    * not expired
    */
    this.activateRoute.params.subscribe(params => {
      this.validateRoomCode(params.roomCode);
    });
  }

  setUserName(): void {
    if (this.username === undefined || this.username.trim() === '') {
      this.snackBar.open('Username is required', '', {
        duration: 3000
      });
      return;
    }
    this.loading = true;
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');
    // before setting the username the validation happens once more just in case and the room is prepared
    this.http
      .get(Globals.ENDPOINT + Globals.ROOM_JOIN_ENDPOINT + this.userService.getTopicName(), { headers, responseType: 'json' })
      .subscribe((response: any) => {
        if (response.status === '1') {
          this.userService.setUserName(this.username);
          this.router.navigate(['messaging']);
        }
        else {
          this.noErrors = false;
          this.errorMessage = response.reason;
        }
      });
  }

  private setRoomCode(roomCode: string): void {
    this.userService.setTopicName(roomCode);
  }

  validateRoomCode(roomCode: string): void {
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json');
    this.http.get(Globals.ENDPOINT + Globals.ROOM_VALIDATE_ENDPOINT + roomCode, { headers, responseType: 'json' })
      .subscribe((response: any) => {
        if (response.status === '1') {
          this.preventRoomEntry = false;
          this.noErrors = true;
          this.setRoomCode(roomCode);
        }
        else {
          this.preventRoomEntry = true;
          this.noErrors = false;
          this.errorMessage = response.reason;
        }
      }, () => {
        this.preventRoomEntry = true;
        this.noErrors = false;
        this.errorMessage = 'The acquaint room you are trying to join does not exist';
      });
  }

}
